@charset "UTF-8";
@font-face {
  font-family: 'SeoulNamsanM';
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_two@1.0/SeoulNamsanM.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

::-moz-selection {
  background-color: #6667ab;
  color: #fff;
}

::selection {
  background-color: #6667ab;
  color: #fff;
}

.profile {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-family: "SeoulNamsanM";
  color: #333;
  border-bottom: 10px solid #6667ab;
  word-break: keep-all;
}

.profile h1,
.profile h2,
.profile h3,
.profile h4,
.profile h5,
.profile h6 {
  letter-spacing: 0.03em;
}

.profile h1 {
  font-size: 3rem;
}

.profile h2 {
  font-size: 2.2rem;
}

.profile h3 {
  font-size: 1.5rem;
}

.profile h4 {
  font-size: 1.3rem;
}

.profile h5 {
  font-size: 1.15rem;
}

.profile h6 {
  font-size: 1rem;
}

.profile .main {
  max-width: 850px;
  margin: 10rem 2rem 5rem;
}

.profile .top {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  width: 100%;
  padding: 1rem;
  text-align: center;
  background: #6667ab;
  background: linear-gradient(-45deg, #6667ab 0%, #a0a2cd 100%);
  font-weight: 600;
  letter-spacing: 0.08rem;
}

.profile .top p {
  color: #fff;
}

.profile .section_title {
  margin: 8rem 0 4rem;
}

.profile .section_title span {
  padding: 0 1rem 0.3rem 0.2rem;
  padding-bottom: 0.3rem;
  border-bottom: 3px solid #6667ab;
}

.profile .btn {
  display: inline-block;
  position: relative;
  margin-right: 1rem;
  padding: 0.5rem;
  background-color: #f5f5f5;
  -webkit-transition: background-color 0.3s;
  transition: background-color 0.3s;
}

.profile .btn::after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #6667ab;
  -webkit-transition: width 0.3s;
  transition: width 0.3s;
}

.profile .btn:hover {
  background-color: #fff;
}

.profile .btn:hover::after {
  width: 100%;
}

.profile .print {
  display: none !important;
}

.profile .labels {
  margin: -0.2rem -0.1rem;
}

.profile .labels .label {
  display: inline-block;
  margin: 0.2rem 0.1rem;
  padding: 0.2rem 0.6rem;
  border-radius: 0.5rem;
  font-size: 1rem;
  background-color: #6667ab;
  color: #fff;
  letter-spacing: 0.05rem;
}

.profile .labels .label.sm {
  padding: 0.1rem 0.5rem;
  font-size: 0.8rem;
}

.personal_info .header {
  padding-bottom: 1.2rem;
  margin-bottom: 2.5rem;
  border-bottom: 3px solid #6667ab;
}

.personal_info .header div {
  position: relative;
}

.personal_info .header .home_btn {
  position: absolute;
  top: 50%;
  right: 0;
  padding: 0.5rem 0.7rem;
  background-color: #6667ab;
  border-radius: 0.5rem;
  color: #fff;
  font-size: 1.4rem;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.personal_info .intro {
  font-size: 1.3rem;
  line-height: 1.7;
}

.personal_info .info {
  margin-top: 3rem;
}

.personal_info .info > div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0.6rem 0;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.personal_info .info > div .info_name {
  display: inline-block;
  position: relative;
  margin-right: 1rem;
  padding: 0.5rem;
  background-color: #f5f5f5;
  font-weight: 600;
  font-size: 1rem;
  vertical-align: middle;
}

.personal_info .info > div .icon {
  margin-right: 0.5rem;
}

.tech_skills .category {
  margin-bottom: 3rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.tech_skills .category + .category {
  padding-top: 2rem;
  border-top: 1px solid #e0e0e0;
}

.tech_skills .sub_category {
  width: 100%;
  margin-top: 2rem;
  padding: 0 2rem;
}

.tech_skills .techs {
  display: -ms-grid;
  display: grid;
  width: 100%;
  gap: 1rem 1rem;
  -ms-grid-columns: (1fr)[3];
      grid-template-columns: repeat(3, 1fr);
  margin-top: 1.8rem;
}

.tech_skills .techs li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.tech_skills .techs li:nth-child(3n + 1) {
  margin-left: 0;
}

.tech_skills .techs li:nth-child(3n) {
  margin-right: 0;
}

.tech_skills .card {
  width: 100%;
  padding: 1.5rem 0.5rem;
  border-radius: 0.5rem;
  -webkit-box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
          box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  text-align: center;
}

.tech_skills .tech_icons {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.tech_skills .tech_icons li + li {
  margin-top: 0.5rem;
}

.tech_skills .tech_icons .icon {
  display: block;
  width: auto;
  height: 2rem;
}

.tech_skills .tech_icons .githubPages {
  padding: 0.2rem 0.5rem;
  display: inline-block;
  width: 5rem;
  border-radius: 0.5rem;
  background-color: #2e71af;
}

.tech_skills .card_main .tech_title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.tech_skills .card_main .tech_title .icon {
  width: 3.6rem;
  margin-right: 0.7rem;
}

.tech_skills .card_main .tech_title .icon.small {
  width: 3.06rem;
}

.tech_skills .card_main .tech_desc {
  margin-top: 1rem;
  padding: 0 1rem;
  line-height: 1.5;
}

.tech_skills .card_sub {
  padding: 0 0.5rem;
  margin-top: 2rem;
}

.tech_skills .card_sub .related_techs {
  padding: 1rem;
  border-top: 1px solid #e0e0e0;
}

.tech_skills .card_sub .related_techs .tech_icons {
  margin-top: 1rem;
}

.tech_skills .card_sub .names {
  margin-top: 0.5rem;
  line-height: 1.3;
}

.tech_skills .card_sub .names span {
  display: inline-block;
  word-break: keep-all;
}

.tech_skills .card_sub .names span:not(:last-of-type)::after {
  content: ',';
  margin-right: 0.3rem;
}

.project {
  margin-bottom: 3rem;
}

.project + .project {
  padding-top: 2rem;
  border-top: 1px solid #e0e0e0;
}

.project .project_title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.project .project_title .page_links {
  margin-left: 2rem;
}

.project .project_contents {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  margin-top: 2.2rem;
}

.project:nth-child(even) .project_contents {
  -webkit-box-orient: horizontal;
  -webkit-box-direction: reverse;
      -ms-flex-direction: row-reverse;
          flex-direction: row-reverse;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.project .project_thumbnail {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.project .project_thumbnail img {
  max-width: 18.75rem;
  padding: 0.3rem;
  border: 3px solid #6667ab;
}

.project:nth-child(odd) .project_info {
  margin-left: 3rem;
}

.project:nth-child(even) .project_info {
  margin-right: 1rem;
}

.project .project_info {
  width: 100%;
}

.project .project_info > ul > li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}

.project .project_info > ul > li + li {
  margin-top: 2rem;
}

.project .project_info > ul > li.page_links h5 {
  line-height: 2;
}

.project .project_info > ul > li h5 {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 8rem;
          flex: 0 0 8rem;
  word-break: keep-all;
  white-space: nowrap;
}

.project .project_info .features li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 1.1rem;
  line-height: 1.5;
}

.project .project_info .features li::before {
  content: '✔';
  margin-right: 0.5rem;
  color: #6667ab;
  vertical-align: middle;
  font-weight: 700;
  font-size: 0.8rem;
}

.project .project_info .features li + li {
  margin-top: 0.4rem;
}

.work_experience .experience {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.work_experience .experience + .experience {
  margin-top: 2rem;
}

.work_experience .experience .company_info {
  margin-right: 3rem;
}

.work_experience .experience .company_info .period {
  margin-top: 1.5rem;
  line-height: 1.6;
  font-size: 1.1rem;
  color: #999;
  letter-spacing: 0.05rem;
}

.work_experience .experience .work_detail h4 {
  margin: 1rem 0.4rem;
  font-weight: 400;
  letter-spacing: 0.06em;
}

.work_experience .experience .work_detail p {
  line-height: 1.7;
  font-size: 1rem;
  color: #666;
}

.work_experience .experience .work_detail ul {
  padding: 0 1rem;
}

.work_experience .experience .work_detail ul li {
  line-height: 1.6;
}

.work_experience .experience .work_detail ul ul {
  margin: 0.5rem 0;
}

.work_experience .experience .work_detail ul ul li {
  line-height: 1.6;
}

.work_experience .experience .work_detail ul ul li::before {
  content: '-';
  margin-right: 0.5rem;
  color: #999;
}

.work_experience .experience .work_detail > ul > li::before {
  content: '';
  display: inline-block;
  width: 4px;
  height: 4px;
  margin-right: 0.2rem;
  background-color: #999;
  vertical-align: middle;
}

@media print {
  html {
    font-size: 14px;
  }
  .profile .main {
    margin-top: 7rem;
    margin-bottom: 2rem;
  }
  .profile .top {
    position: absolute;
  }
  .profile .print {
    display: block !important;
  }
  .profile .no_print {
    display: none !important;
  }
  .profile .section_title {
    margin: 7rem 0 3rem;
  }
  .profile .labels .label {
    padding-top: 0.3rem;
    padding-bottom: 0.3rem;
  }
  .tech_skills .category {
    margin-bottom: 1.5rem;
  }
  .tech_skills .other_techs {
    margin-bottom: 0;
  }
  .tech_skills .techs {
    display: block;
    margin-top: 0.8rem;
  }
  .tech_skills .card {
    padding-top: 1rem;
    padding-bottom: 1rem;
    -webkit-box-shadow: none;
            box-shadow: none;
    text-align: left;
  }
  .tech_skills .card_main {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: start;
        -ms-flex-align: start;
            align-items: flex-start;
  }
  .tech_skills .card_main .tech_title {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 12rem;
            flex: 0 0 12rem;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
  }
  .tech_skills .card_main .tech_title .icon {
    width: 2.5rem;
  }
  .tech_skills .card_main .tech_title .icon.small {
    width: 2rem;
  }
  .tech_skills .card_main .tech_desc {
    margin: 0;
    padding: 0.5rem 0;
  }
  .tech_skills .card_sub {
    margin: 0;
    padding: 0 0 0 12rem;
  }
  .tech_skills .card_sub .related_techs {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding: 1rem;
    border: 1px solid #e0e0e0;
    border-radius: 0.3rem;
  }
  .tech_skills .card_sub .related_techs .names {
    margin: 0 0 0 1rem;
  }
  .projects .project {
    padding-left: 1rem;
    padding-right: 1rem;
    margin-bottom: 2rem;
  }
  .projects .project .project_contents {
    display: block;
    margin-top: 1.2rem;
  }
  .projects .project .project_thumbnail {
    width: 18rem;
  }
  .projects .project .project_info {
    margin: 0;
  }
  .projects .project .project_info > ul > li + li {
    margin-top: 1.3rem;
  }
  .projects .project .project_info h5 {
    margin-bottom: 0;
  }
  .projects .project .features,
  .projects .project .labels {
    padding: 0 1rem;
  }
}

@media print, screen and (max-width: 68.25rem) {
  .project img {
    max-width: 220px;
  }
}
